/* FOOTER AND MENU */

.footer {
  width: 100%;
  height: 100%;
  
  text-align: center;
}


.footer--day {
  background-color: #F2F2F2;
  -webkit-box-shadow: 0px -4px 10px hsla(0, 0%, 0%, 0.2);
          box-shadow: 0px -4px 10px hsla(0, 0%, 0%, 0.2);
}

.footer--night {
  background-color: rgb(30, 30, 50);
  -webkit-box-shadow: 0px -4px 10px rgb(0, 0, 0);
          box-shadow: 0px -4px 10px rgb(0, 0, 0);
}

.button-container {

  height: 12vh;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  
  position: absolute;
  bottom: 0.5em;

  width: 100%;
} 


